// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import Vuex from 'vuex'
import VueResource from 'vue-resource'

Vue.use(Vuex)
Vue.use(VueResource)

const store = new Vuex.Store({
	state: {
		news: []
	},
	mutations: {
		getNews (state, news){
			state.news = news.data.data
		}
	},
	actions: {
		getNews (context, cat){
			Vue.http.get('http://127.0.0.1:5000/api/getNews?cat='+ cat.name).then((data)=>{context.commit('getNews', {'data': data.json()})})
		}
	}
})

new Vue({
  el: '#app',
  store,
  template: '<App/>',
  components: { App }
})



